.darktheme__button {
    position: relative;
    font-size: 20px;
    padding: 0;
    background-color: transparent;
    width: 30px;
    height: 30px;
    transition: transform var(--main-effect);
    
    &:hover {
        transform: rotate(-20deg) scale(1.25);
    }
        @include minMedia($tab) {
            font-size: 30px;
    }
}
